.popup__container{
  width: 430px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
}

@media screen and (max-width: 420px){
  .popup__container{
    width: 282px;
  }
}